                      </el-autocomplete>
                      <el-button v-else class="penguin-new-tag-btn" size="small" @click="showInput">+ New Tag</el-button>
                    </el-form-item>
                    <!-- TAGs -->
                  </el-col>
                </el-row>
                <el-row>
                  <el-col :span="24">
                    <el-form-item label="">
                      <el-radio-group v-model="problemData.category">
                        <el-radio label="" border></el-radio>
                        <el-radio label="" border></el-radio>
                        <el-radio label="" border></el-radio>
                        <el-radio label="" border></el-radio>
                      </el-radio-group>
                    </el-form-item>
                  </el-col>
                </el-row>
                <el-form-item>
                  <label slot="label">
                     (Description)
                    <img src="/static/md-icon.png" alt="md-icon" style="width: 25px; margin-left: 10px;">
                    <img src="/static/katex-icon.png" alt="md-icon" style="width: 40px; margin-left: 3px;">
                  </label>
                  <div>
                    <el-input class="width-100" type="textarea" rows="5" resize="vertical" placeholder="" v-model="problemData.description" @input="update"></el-input>
                    <vue-markdown class="penguin-md-preview" :source="problemData.description"></vue-markdown>
                  </div>
                </el-form-item>
                <el-row>
                  <el-col :span="12">
                    <el-form-item label=" (Input)">
                      <el-input type="textarea" rows="3" resize="none" placeholder="" v-model="problemData.inputDesc" style="width: 90%;"></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="11" :offset="1">
                    <el-form-item label=" (Output)">
                      <el-input type="textarea" rows="3" resize="none" placeholder="" v-model="problemData.outputDesc" style="width: 100%;"></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
                <!--  -->
                <transition-group name="slide-fade">
                  <el-row v-for="(sample, index) in problemData.testCases" :key="index+1">
                    <el-col :span="12">
                      <el-form-item>
                        <span slot="label">{{index+1}} (Input Sample{{index+1}})</span>
                        <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.inputSample" style="width: 90%;"></el-input>
                      </el-form-item>
                    </el-col>
                    <el-col :span="11" :offset="1">
                      <el-form-item>
                        <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
                        <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.outputSample" style="width: 100%;"></el-input>
                      </el-form-item>
                    </el-col>           
                  </el-row>
                </transition-group>
                <el-row>
                  <el-col :span="24">
                    <p style="color: #F56C6C; margin-top: 0px;"> </p>
                  </el-col> 
                </el-row>
                <el-button size="small" type="primary" plain @click="addSample">+ </el-button>
                <el-button size="small" type="danger" plain @click="delSample">- </el-button>
                <!--  -->
                <el-row>
                  <el-button type="primary" @click="dialogFormVisible=true" style="float: right;"></el-button>
                </el-row>
              </el-form>
            </el-col>
          </el-row>
        </el-main>
      </el-container>
    </el-container>
  </el-container>

  <!--  dialog start -->
  <el-dialog id="confirmProblemDialog" title="" :visible.sync="dialogFormVisible" v-loading="loading">
    <el-form :model="form">
      <el-row>
        <el-col :span="20" :offset="2">
          <el-form-item label="">
            <el-radio-group v-model="problemData.category" disabled>
              <el-radio label="" border></el-radio>
              <el-radio label="" border></el-radio>
              <el-radio label="" border></el-radio>
              <el-radio label="" border></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20" :offset="2">
          <!-- TAGs -->
          <el-form-item label="">
            <el-tag :key="tag" v-for="tag in problemData.tag">
              {{tag}}
            </el-tag>
            <el-autocomplete class="input-new-tag" v-if="inputVisible" ref="saveTagInput" size="small" @keyup.enter.native="handleInputConfirm" v-model="inputValue" popper-class="my-autocomplete" :fetch-suggestions="querySearch" @select="handleSelect">
              <template slot-scope="{ item }">
                <div class="name">{{ item.value }}</div>
              </template>
            </el-autocomplete>
          </el-form-item>
          <!-- TAGs -->
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12" :offset="2">
          <el-form-item label="">
            <el-input v-model="problemData.name" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20" :offset="2">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="problemData.description" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="9" :offset="2">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="problemData.inputDesc" readonly></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="9" :offset="1">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="problemData.outputDesc" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-for="(sample, index) in problemData.testCases" :key="index">
        <el-col :span="9" :offset="2">
          <el-form-item>
            <span slot="label">{{index+1}} (Input Sample{{index+1}})</span>
            <el-input type="textarea" rows="3" resize="none" v-model="sample.inputSample" readonly></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="9" :offset="1">
          <el-form-item>
            <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
            <el-input type="textarea" rows="3" resize="none" v-model="sample.outputSample" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="22" :offset="2">
          <p style="color: #F56C6C; margin-top: 0px;"> </p>
        </el-col> 
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialogFormVisible=false"> </el-button>
      <el-button type="primary" @click="newProblem"> </el-button>
    </div>
  </el-dialog>
  <!--  dialog end -->
</div>
</template>

<script>
import {quesbankCheckLogin} from '@/apis/_checkLogin.js'
import {apiAddProblem} from '@/apis/problemBank.js'

import VueMarkdown from 'vue-markdown'
import DateUtil from '@/utils/DateUtil.js'
import problemStateMixin from '@/mixins/problemState.mixin.js'

import NavHeaderQues from '@/components/Ques/NavHeaderQues'
import SideNavQues from '@/components/Ques/SideNavQues'
import PageNameBreadcrumb from '@/components/MgmtContent/PageNameBreadcrumb'

import '@/assets/css/vendors/markdownParser/marked.js'
import '@/assets/css/vendors/markdownParser/lodash.js'

export default {
  components: {
    NavHeaderQues,
    SideNavQues,
    PageNameBreadcrumb,
    VueMarkdown
  },
  mixins: [problemStateMixin],
  data() {
    return {
      dialogFormVisible: false,
      problemData: {
        'name': '',
        'category': '',
        'tag': [],
        'description': '',
        'inputDesc': '',
        'outputDesc': '',
        'testCases': [{
          'inputSample': '',
          'outputSample': ''
        }, {
          'inputSample': '',
          'outputSample': ''
        }]
      },
      // tags
      inputVisible: false,
      inputValue: '',
      autoCompleteTags: [],
      // loading
      loading: false
    }
  },
  mounted() {
    quesbankCheckLogin();
    this.autoCompleteTags = this.problemTag;
  },
  methods: {
    //markdown
    update: _.debounce(function(e) {
      // console.log(e.target.value);
      this.problemData.description = e;
      // this.problemData.description = e.target.value
    }, 300),
    newProblem() {
      if (this.problemData.name == '') {
        this.$message.error('');
      } else if (this.problemData.category == '') {
        this.$message.error('');
      } else if (this.problemData.tag.length == 0) {
        this.$message.error('');
      } // java, pytag
        else if (!(this.problemData.tag.includes('Java')||this.problemData.tag.includes('Python'))) {
        this.$message.error('');
      } else if (this.problemData.description == '') {
        this.$message.error('');
      } else if (this.problemData.inputDesc == '') {
        this.$message.error('');
      } else if (this.problemData.outputDesc == '') {
        this.$message.error('');
      } else if (this.problemData.testCases.length == 0) {
        this.$message.error('');
      } else if (this.problemData.testCases.length == 1) {
        this.$message.error('');
      } else {
        this.loading = true;
        
        apiAddProblem({
          name: this.problemData.name,
          category: this.problemData.category,
          tag: this.problemData.tag,
          description: this.problemData.description,
          inputDesc: this.problemData.inputDesc,
          outputDesc: this.problemData.outputDesc,
          testCases: this.problemData.testCases
        }).then((response) => {
          let res = response.data;
          if (res.status == '200') {
            this.$message({
              type: 'success',
              message: '!'
            });
            this.problemData = {
              'name': '',
              'category': '',
              'tag': [],
              'description': '',
              'inputDesc': '',
              'outputDesc': '',
              'testCases': [{
                'inputSample': '',
                'outputSample': ''
              }, {
                'inputSample': '',
                'outputSample': ''
              }]
            }
            this.dialogFormVisible = false;
            this.loading = false;
          } else {
            this.loading = false;
            this.$message.error(res.msg);
          }
        });
      }
    },
    // tags control methods
    handleClose(tag) {
      this.problemData.tag.splice(this.problemData.tag.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.problemData.tag.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    // tags autocomplete
    querySearch(queryString, cb) {
      var autoCompleteTags = this.autoCompleteTags;
      var results = queryString ? autoCompleteTags.filter(this.createFilter(queryString)) : autoCompleteTags;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (tag) => {
        return (tag.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.problemData.tag.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    addSample() {
      let obj = {
        'inputSample': '',
        'outputSample': ''
      }
      this.problemData.testCases.push(obj);
    },
    delSample() {
      this.problemData.testCases.pop();
    }
  }
}
</script>

<style>
#confirmProblemDialog .el-loading-mask {
  height: 160% !important;
}
</style>