<template>
<div>
  <el-row class="penguin-box border-radius-none" id="newproblem-section">
    <el-col class="mb-2" :span="20" :offset="2">
      <el-button type="warning" icon="fas fa-book" round @click="viewProblemBank"> </el-button>
    </el-col>
    <el-col :span="20" :offset="2">
      <el-form :model="problemData" label-width="100px" label-position="top">
        <el-row>
          <el-col :span="12">
            <el-form-item label="">
              <el-input class="width-70" v-model="problemData.name" maxlength="15" placeholder="15"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <!-- TAGs -->
            <el-form-item label="">
              <el-tag :key="tag" v-for="tag in problemData.tag" closable :disable-transitions="false" @close="handleClose(tag)">
                {{tag}}
              </el-tag>
              <el-autocomplete class="penguin-auto-input" v-if="inputVisible" ref="saveTagInput" size="small" 
              @keyup.enter.native="handleInputConfirm" v-model="inputValue" popper-class="my-autocomplete" :fetch-suggestions="querySearch" @select="handleSelect">
                <template slot-scope="{ item }">
                  <div class="name">{{ item.value }}</div>
                </template>
              </el-autocomplete>
              <el-button v-else class="penguin-new-tag-btn" size="small" @click="showInput">+ New Tag</el-button>
            </el-form-item>
            <!-- TAGs -->
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="">
              <el-radio-group v-model="problemData.category">
                <el-radio label="" border></el-radio>
                <el-radio label="" border></el-radio>
                <el-radio label="" border></el-radio>
                <el-radio label="" border></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="" style="margin-bottom:0px;">
              <el-select class="width-70" v-model="problemData.type" placeholder="" @change="changeProblemType">
                <el-option  v-for="item in problemType" :key="item.value" :label="item.label" :value="item.value"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="">
              <el-date-picker class="width-70" type="datetime" placeholder="" v-model="problemData.deadline" @change="selectDeadline"></el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <!-- pattern -->
        <el-row>
          <el-col class="mb-4" :span="24">
            <el-form-item class="mb-1" label=" (Pattern)">
              <el-input class="width-80 mb-2" v-model="pattern.pat" placeholder="" v-for="(pattern, index) in problemData.pattern" :key="index"></el-input>
            </el-form-item>
            <el-button size="small" type="primary" plain @click="addPattern">+ pattern</el-button>
            <el-button size="small" type="danger" plain @click="delPattern">- pattern</el-button>
          </el-col>
        </el-row>
        <!-- pattern -->
        <el-form-item>
          <label slot="label">
             (Description)
            <img src="/static/md-icon.png" alt="md-icon" style="width: 25px; margin-left: 10px;">
            <img src="/static/katex-icon.png" alt="md-icon" style="width: 40px; margin-left: 3px;">
          </label>
          <div>
            <el-input class="width-100" type="textarea" rows="5" resize="vertical" placeholder="" v-model="problemData.description" @input="update"></el-input>
            <vue-markdown class="penguin-md-preview" :source="problemData.description"></vue-markdown>
          </div>
        </el-form-item>
        <el-row>
          <el-col :span="12">
            <el-form-item label=" (Input)">
              <el-input type="textarea" rows="3" resize="none" placeholder="" v-model="problemData.inputDesc" style="width: 90%;"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="11" :offset="1">
            <el-form-item label=" (Output)">
              <el-input type="textarea" rows="3" resize="none" placeholder="" v-model="problemData.outputDesc" style="width: 100%;"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <!--  -->
        <transition-group name="slide-fade">
          <el-row v-for="(sample, index) in problemData.testCases" :key="index+1">
            <el-col :span="12">
              <el-form-item>
                <span slot="label">{{index+1}} (Input Sample{{index+1}})</span>
                <el-input class="width-90" type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.inputSample"></el-input>
              </el-form-item>
            </el-col>

            <!-- TODO:  -->
            <el-col :span="11" :offset="1">
              <el-form-item>
                <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
                <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.outputSample1" style="width: 100%;"></el-input>
              </el-form-item>
            </el-col>
            <!-- TODO:  -->
            <el-col :span="11" :offset="13">
              <el-form-item>
                <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
                <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.outputSample2" style="width: 100%;"></el-input>
              </el-form-item>
            </el-col>

          </el-row>
        </transition-group>
        <el-row>
          <el-col :span="24">
            <p class="mt-0 text-danger"> </p>
          </el-col> 
        </el-row>
        <el-button size="small" type="primary" plain @click="addSample">+ </el-button>
        <el-button size="small" type="danger" plain @click="delSample">- </el-button>
        <!--  -->
        <!--  start-->
        <el-row v-if="problemData.type==''">
          <el-col class="mb-3" :span="24">
            <el-divider content-position="center"></el-divider>
            <el-cascader-panel v-loading="discussLoading" v-model="discussValue" :options="discussOptions" :props="discussProps" style="height:500px;"></el-cascader-panel>
          </el-col>
        </el-row>
        <!--  end -->
        <el-row>
          <el-button class="float-right" type="primary" @click="previewProblem"></el-button>
        </el-row>
      </el-form>
    </el-col>
  </el-row>

  <!--  dialog -->
  <confirm-problem-dialog :visible="confirmProblemDialogVisible" :data="problemData" @onChangeVisible="onChangeConfirmProblemDialogVisible" @refreshProblemData="refreshProblemData"></confirm-problem-dialog>

  <!--  dialog start -->
  <problem-bank-dialog :visible="problemBankDialogVisible" :data="quesList" @onChangeVisible="onChangeProblemBankDialogVisible" @importProblem="importProblem"></problem-bank-dialog>
</div>
</template>

<script>
import {apiAllStud} from '@/apis/student.js'
import {apiGetAllProblem, apiGetProblemInfo} from '@/apis/problemBank.js'

import VueMarkdown from 'vue-markdown'
import DateUtil from '@/utils/DateUtil.js'
import GeneralUtil from '@/utils/GeneralUtil.js'
import problemStateMixin from '@/mixins/problemState.mixin.js'

import ConfirmProblemDialog from '@/components/MgmtProblem/ConfirmProblemDialog'
import ProblemBankDialog from '@/components/MgmtProblem/ProblemBankDialog'

import '@/assets/css/vendors/markdownParser/marked.js'
import '@/assets/css/vendors/markdownParser/lodash.js'

export default {
  components: {
    VueMarkdown,
    ConfirmProblemDialog,
    ProblemBankDialog
  },
  mixins: [problemStateMixin],
  data() {
    return {
      confirmProblemDialogVisible: false, // dialog
      problemData: {
        'name': '',
        'type': '',
        'category': '', // ...
        'tag': [],
        'deadline': '',
        'previewDate': '', // deadline
        'description': '',
        'inputDesc': '',
        'outputDesc': '',
        'testCases': [{
          'inputSample': '',
          'outputSample1': '',
          'outputSample2': ''
        }, {
          'inputSample': '',
          'outputSample1': '',
          'outputSample2': ''
        }],
        'pattern': [{
          'pat': ''
        }],
        'discussValue': [], // pair
        'previewDiscussTreeData': [] // tree
      },
      previewDiscussTreeData: [], // 
      // tags
      inputVisible: false,
      inputValue: '',
      autoCompleteTags: [],
      // 
      discussLoading: false,
      discussStudsList: [], // 
      discussNum: 0,
      discussNowFocus: '', // 
      discussNumControl: {}, // {:count}
      discussIsLock: false,
      discussValue: [], // (pairs)
      discussProps: { 
        multiple: true
      },
      discussOptions: [],
      // problemBank Dialog
      problemBankDialogVisible: false,
      quesList: [],
    }
  },
  mounted() {
    this.autoCompleteTags = this.problemTag;
  },
  methods: {
    //markdown
    update: _.debounce(function(e) {
      this.problemData.description = e;
    }, 300),
    selectDeadline() {
      this.problemData.previewDate = DateUtil.formatDatetime(this.problemData.deadline);
    },
    // 
    getStudsList() {
      apiAllStud({
        courseId: this.$store.state.course.courseInfo.courseId
      }).then((response) => {
        let res = response.data;
        if (res.status == '200') {
          this.discussStudsList = res.result;
        }
      });

      return new Promise(function (resolve, reject) {
        setTimeout(function () {
          resolve(true);
        }, 1000);
      });
    },
    changeProblemType() {
      // 
      if (this.problemData.type == '') {
        this.discussLoading = true;
        this.getStudsList().then((value) => {
          if(value == true) {
            // discussOptions
            this.discussStudsList.forEach((element) => {
              let obj = {
                value: element,
                label: element
              }
              this.discussOptions.push(obj);

              // discussNumControl, 
              this.discussNumControl[element] = 0;
            });

            // discussOptionsobj
            this.discussOptions.forEach((ele, idx) => {
              let [...tempStuList] = this.discussStudsList;
              // tempStuList.push(ele.value);
              let noMyselfList = GeneralUtil.removeInArray(tempStuList, ele);
              
              let children = [];
              this.discussOptions[idx].children = [];

              noMyselfList.forEach((ele) => {
                let obj = {
                  value: ele,
                  label: ele
                }
                this.discussOptions[idx].children.push(obj);
              });
            });
            this.discussLoading = false;
          }
        }).catch(function (error) {
          console.log(error);
        });
      } else { // 
        this.discussOptions = [];
      }
    },
    // tags control methods
    handleClose(tag) {
      this.problemData.tag.splice(this.problemData.tag.indexOf(tag), 1);
    },
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.problemData.tag.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    // tags autocomplete
    querySearch(queryString, cb) {
      var autoCompleteTags = this.autoCompleteTags;
      var results = queryString ? autoCompleteTags.filter(this.createFilter(queryString)) : autoCompleteTags;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return (tag) => {
        return (tag.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    handleSelect(item) {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.problemData.tag.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = '';
    },
    addPattern() {
      let obj = { 'pat': '' };
      this.problemData.pattern.push(obj);
    },
    delPattern() {
      this.problemData.pattern.pop();
    },
    addSample() {
      let obj = {
        'inputSample': '',
        'outputSample1': '',
        'outputSample2': ''
      }
      this.problemData.testCases.push(obj);
    },
    delSample() {
      this.problemData.testCases.pop();
    },
    // confirm problem dialog
    previewProblem() {
      // discussValuetree
      let treeData = [];
      this.discussOptions.forEach((element) => {
        let obj = {
          label: element.label,
          children: []
        }
        treeData.push(obj);
      });

      this.discussValue.forEach((element) => {
        let cor = element[0]; // 
        let cored = element[1]; // 
        
        treeData.forEach((ele) => {
          if (cor == ele.label) {
            let obj = {
              label: cored
            }
            ele.children.push(obj);
          }
        });
      })
      this.previewDiscussTreeData = treeData; // xx

      // problemData
      this.problemData.discussValue = this.discussValue; // pair
      this.problemData.previewDiscussTreeData = treeData;

      this.confirmProblemDialogVisible = true;
    },
    onChangeConfirmProblemDialogVisible(val) {
      this.confirmProblemDialogVisible = val;
    },
    refreshProblemData() {
      this.problemData = {
        'name': '',
        'type': '',
        'category': '', // ...
        'tag': [],
        'deadline': '',
        'previewDate': '', // deadline
        'description': '',
        'inputDesc': '',
        'outputDesc': '',
        'testCases': [{
          'inputSample': '',
          'outputSample1': '',
          'outputSample2': ''
        }, {
          'inputSample': '',
          'outputSample1': '',
          'outputSample2': ''
        }],
        'pattern': [{
          'pat': ''
        }],
        'discussValue': [], // pair
        'previewDiscussTreeData': [] // tree
      }
    },
    // problemBank Dialog
    onChangeProblemBankDialogVisible(val) {
      this.problemBankDialogVisible = val;
    },
    viewProblemBank() {
      this.problemBankDialogVisible = true;

      apiGetAllProblem().then((response) => {
        let res = response.data;
        if (res.status == '200') {
          this.quesList = res.result;
        }
      });
    },
    importProblem(id) {
      apiGetProblemInfo({
        id: id
      }).then((response) => {
        let res = response.data;
        if (res.status == '200') {
          this.problemData.name = res.result.name;
          this.problemData.category = res.result.category;
          this.problemData.tag = res.result.tag;
          this.problemData.description = res.result.description;
          this.problemData.inputDesc = res.result.inputDesc;
          this.problemData.outputDesc = res.result.outputDesc;
          this.problemData.testCases = res.result.testCases;

          this.$message({type: 'success', message: '!'});
          this.problemBankDialogVisible = false;
        }
      });
    }
  }
}
</script>

<style lang="scss">
/* discuss cascader */
#newproblem-section .el-cascader-menu__wrap {
  height: 500px !important;
}
</style>