/**
 * Functions of processing date format
 * Created by Ching-Hsuan Su on 2018/06/22.
 */

export default class DateUtil {

  /**
   * @desc  e.g. 2020-02-07 21:01:30
   * @param oriDate
   * @return {string} 
   */
  static formatDatetime(oriDate) {
    let d = new Date(oriDate);

    let date = d.getFullYear() + "-" + (d.getMonth() + 1) + "-" + d.getDate();

    // 0
    let hh = ('0' + d.getHours()).substr(-2);
    let mm = ('0' + d.getMinutes()).substr(-2);
    let ss = ('0' + d.getSeconds()).substr(-2);

    // let hh = d.getHours();
    // let mm = d.getMinutes();
    // let ss = d.getSeconds();

    let time = hh + ":" + mm + ":" + ss; 

    return date + ' ' + time
  }

  /**
   * @desc 
   * @param {string} sDate1
   * @param {string} sDate2
   * @return {int} 
   */
  static dateDiff(sDate1, sDate2) { // sDate1sDate22018-6-18 10:23:10
    var aDate, oDate1, oDate2, iDays;
    var splitDatetime1 = sDate1.split(' ');
    aDate = splitDatetime1[0].split("-");
    oDate1 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0]); // 6-18-2018

    var splitDatetime2 = sDate2.split(' ');
    aDate = splitDatetime2[0].split("-");
    oDate2 = new Date(aDate[1] + '-' + aDate[2] + '-' + aDate[0]);

    iDays = parseInt(Math.abs(oDate1 - oDate2) / 1000 / 60 / 60 / 24); // 
    // console.log(iDays);
    return iDays
  }

  /**
   * @desc 
   * @param {string} date
   * @return {string} 
   */
  static nextDayDate(date) {
    var date = new Date(date);
    date.setDate(date.getDate() + 1);
    var nextDayDate = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    return nextDayDate
  }

  /**
   * @desc 
   * @return {string} 
   */
  static getTodayDate() {
    let date = new Date();
    let today = date.getFullYear() + "-" + (date.getMonth() + 1) + "-" + date.getDate();
    return today
  }

  /**
   * @desc deadline
   * @param {string} deadline
   * @return {boolean} truefalse
   */
  static isOverDate(deadline) {
    let deadlineDatetime = new Date(deadline);
    let todayDatetime = new Date();

    if(todayDatetime.valueOf() > deadlineDatetime.valueOf()) { // 
      return true
    } else { // 
      return false
    }
  }

}
