<template>
<div>
  <!-- ProblemsData,  -->
  <el-dialog :visible.sync="myVisible" @close="doInfoDialogActiveStudentCode=false;">
    <el-row v-if="doInfoDialogActiveStudentCode==false">
      <el-col :span="20" :offset="2">
        <div class="penguin-content-header">
          <div class="content-title">{{ problemName }}</div>
        </div>
        <el-table class="width0-100 mb-12" max-height="336" :data="data">
          <el-table-column prop="studentId" label="">
          </el-table-column>
          <el-table-column prop="studentName" label=""></el-table-column>
          <el-table-column prop="score" label=""></el-table-column>
          <el-table-column v-if="status=='done'" label="">
            <template slot-scope="scope">
              <span><a class="text-primary text-clean" @click="seeStudentCode(scope.row)" href="javascript:void(0)"><i class="fas fa-code"></i> </a></span>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row v-if="doInfoDialogActiveStudentCode">
      <el-col>
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>
            <a class="text-clean" href="javascript:void(0)" @click="doInfoDialogActiveStudentCode=false">{{problemName}}</a>
          </el-breadcrumb-item>
          <el-breadcrumb-item>{{doInfoDianlogNowStudentId}} {{doInfoDianlogNowStudentName}}</el-breadcrumb-item>
        </el-breadcrumb>
        <codemirror class="mt-4" v-model="doInfoDianlogNowStudentCode" :options="options"></codemirror>
        <a class="hyperlink float-right mt-2" @click="copy(doInfoDianlogNowStudentCode)"><i class="el-icon-document"></i></a>
      </el-col>
    </el-row>
  </el-dialog>
</div>
</template>

<script>
import { codemirror } from 'vue-codemirror-lite'
import GeneralUtil from '@/utils/GeneralUtil.js'

export default {
  components: {
    codemirror
  },
  props: ['visible', 'data', 'status', 'problemName'],
  data() {
    return {
      myVisible: this.visible,
      doInfoDialogActiveStudentCode: false, // code
      doInfoDianlogNowStudentId: '', // 
      doInfoDianlogNowStudentName: '', // 
      doInfoDianlogNowStudentCode: '', // code
      // codemirror options
      options: {
        mode: 'text/x-java',
        theme: 'default',
        indentUnit: 4, // 
        tabSize: 4,
        lineNumbers: true,
        matchBrackets: true, // 
        smartIndent: true, // 
        autoCloseBrackets: true, // 
        viewportMargin: Infinity,
        gutter: true,
        fixedGutter: true,
        extraKeys: {
          'Ctrl-Space': 'autocomplete'
        },
        showCursorWhenSelecting: true, // 
        // 
        foldGutter: true,
        gutters: ['CodeMirror-linenumbers', 'CodeMirror-foldgutter'],
        readOnly: 'nocursor'
      },
    }
  },
  watch: {
    visible(val) {
      this.myVisible = val;
    },
    myVisible(val) {
      this.$emit('onChangeVisible', val)
    }
  },
  methods: {
    seeStudentCode(data) {
      this.doInfoDianlogNowStudentId = data.studentId;
      this.doInfoDianlogNowStudentName = data.studentName;
      this.doInfoDianlogNowStudentCode = data.code;
      this.doInfoDialogActiveStudentCode = true;
    },
    copy(s) {
      // 
      GeneralUtil.copy(s);
      // success msg
      this.$message({
        message: '',
        type: 'success'
      });
    }
  }
}
</script>