<template>
<div>
  <el-dialog id="confirmProblemDialog" title="" :visible.sync="myVisible" v-loading="loading" @close="closeDialog">
    <el-form>
      <el-row>
        <el-col :span="20" :offset="2">
          <el-form-item label="">
            <el-radio-group v-model="data.category" disabled>
              <el-radio label="" border style="margin-right: 5px;"></el-radio>
              <el-radio label="" border style="margin-right: 5px;"></el-radio>
              <el-radio label="" border style="margin-right: 5px;"></el-radio>
              <el-radio label="" border style="margin-right: 5px;"></el-radio>
            </el-radio-group>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20" :offset="2">
          <!-- TAGs -->
          <el-form-item label="" class="mr-10-label">
            <el-tag :key="tag" v-for="tag in data.tag">
              {{tag}}
            </el-tag>
          </el-form-item>
          <!-- TAGs -->
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6" :offset="2">
          <el-form-item label="">
            <el-input v-model="data.name" readonly></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :offset="1">
          <el-form-item label="">
            <el-input v-model="data.type" readonly></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="6" :offset="1">
          <el-form-item label="">
            <el-input v-model="data.previewDate" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <!-- pattern -->
      <el-row>
        <el-col :span="20" :offset="2">
          <el-form-item label=" (Pattern)">
            <el-input v-model="pattern.pat" readonly maxlength="15" style="width: 100%; margin-bottom: 10px;" v-for="(pattern, index) in data.pattern" :key="index"></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="20" :offset="2">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="data.description" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="9" :offset="2">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="data.inputDesc" readonly></el-input>
          </el-form-item>
        </el-col>
        <el-col :span="10" :offset="1">
          <el-form-item label="">
            <el-input type="textarea" rows="3" resize="none" v-model="data.outputDesc" readonly></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row v-for="(sample, index) in data.testCases" :key="index">
        <el-col :span="9" :offset="2">
          <el-form-item>
            <span slot="label">{{index+1}} (Input Sample{{index+1}})</span>
            <el-input type="textarea" rows="3" resize="none" v-model="sample.inputSample" readonly></el-input>
          </el-form-item>
        </el-col>

        <!-- TODO:  -->
        <el-col :span="10" :offset="1">
          <el-form-item>
            <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
            <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.outputSample1" style="width: 100%;"></el-input>
          </el-form-item>
        </el-col>
        <!-- TODO:  -->
        <el-col :span="10" :offset="12">
          <el-form-item>
            <span slot="label">{{index+1}} (Output Sample{{index+1}})</span>
            <el-input type="textarea" rows="3" resize="vertical" placeholder="" v-model="sample.outputSample2" style="width: 100%;"></el-input>
          </el-form-item>
        </el-col>

      </el-row>
      <el-row>
        <el-col :span="22" :offset="2">
          <p style="color: #F56C6C; margin-top: 0px;"> </p>
        </el-col> 
      </el-row>
      <el-row v-if="data.type==''">
        <el-col :span="20" :offset="2">
          <!-- Tree -->
          <p></p>
          <el-tree class="discussTree" :data="data.previewDiscussTreeData"></el-tree>
        </el-col>
      </el-row>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="closeDialog"> </el-button>
      <el-button type="primary" @click="newProblem"> </el-button>
    </div>
  </el-dialog>
</div>
</template>

<script>
import {apiAddProblem} from '@/apis/problem.js'
import {apiCreateTeam} from '@/apis/team.js'

import DateUtil from '@/utils/DateUtil.js'

export default {
  components: {},
  props: ['visible', 'data'],
  data() {
    return {
      loading: false, // loading
      myVisible: this.visible,
      // preview tree data
      previewDiscussTreeData: [] // 
    }
  },
  watch: {
    visible(val) {
      this.myVisible = val;
    },
    myVisible(val) {
      this.$emit('onChangeVisible', val)
    }
  },
  methods: {
    closeDialog() {
      this.myVisible = false;
    },
    newProblem() {
      if (this.data.name == '') {
        this.$message.error('');
      } else if (this.data.type == '') {
        this.$message.error('');
      } else if (this.data.category == '') {
        this.$message.error('');
      } else if (this.data.tag.length == 0) {
        this.$message.error('');
      } // java, pytag
        else if (!(this.data.tag.includes('Java')||this.data.tag.includes('Python'))) {
        this.$message.error('');
      } else if (this.data.deadline == '') {
        this.$message.error('');
      } else if (this.data.description == '') {
        this.$message.error('');
      } else if (this.data.inputDesc == '') {
        this.$message.error('');
      } else if (this.data.outputDesc == '') {
        this.$message.error('');
      } else if (this.data.testCases.length == 0) {
        this.$message.error('');
      } else if (this.data.testCases.length == 1) {
        this.$message.error('');
      } else {
        this.loading = true;

        // pattern
        let patternArray = [];
        if (this.data.pattern.length==1 && this.data.pattern[0].pat=='') {
          patternArray = [];
        } else {
          this.data.pattern.forEach((element) => {
            patternArray.push(element.pat);
          });
        }
        
        apiAddProblem({
          courseId: this.$store.state.course.courseInfo.courseId,
          name: this.data.name,
          type: this.data.type,
          category: this.data.category,
          tag: this.data.tag,
          description: this.data.description,
          deadline: this.data.previewDate,
          inputDesc: this.data.inputDesc,
          outputDesc: this.data.outputDesc,
          testCases: this.data.testCases,
          keyword: [],
          pattern: patternArray
        }).then((response) => {
          let res = response.data;
          if (res.status == '200') {
            this.$message({
              type: 'success',
              message: '!'
            });
            
            // 
            if (this.data.type == '') {
              // [[correctAccount, correctedAccount]][{correctAccount, correctedAccount}]
              let discussValueObject = []; // [{correctAccount, correctedAccount}]
              this.data.discussValue.forEach((element) => {
                let object = {
                  correctAccount: element[0],
                  correctedAccount: element[1]
                }
                discussValueObject.push(object);
              });

              apiCreateTeam({
                problemId: res.result.problemId,
                pairs: discussValueObject
              }).then((response) => {
                let res = response.data;
                if (res.status == 200) {
                  console.log('team');
                }
              });
            }

            this.$emit('refreshProblemData');

            this.myVisible = false;
            this.loading = false;
          } else {
            this.loading = false;
            this.$message.error(res.msg);
          }
        }).catch((error) => {
          this.loading = false;
          this.$message.error('');
          console.log(error);
        });
      }
    },
  }
}
</script>

