let tagToLogicTable = {
  'Java': {
    '': ['if', 'if else'],
    '': ['for', 'while'],
    'for': ['for'],
    'while': ['while'],
    'switch': ['switch']
  }
}

export default class KeyPatUtil {
  /**
   * @desc tag, keywords
   * @param {array} t tags
   * @return {array} keywords
   */
  static toKeys(t) {
    let [...tags] = t;
    let langs = ['Java', 'Python'];
    let nowLang = ''; // lang
  
    // 
    langs.forEach((lang) => {
      let index = tags.indexOf(lang);
      // console.log(index);
      if (index>=0) {
        nowLang = tags[index];
        tags.splice(index, 1);
      }
    });
  
    // keywords
    let keywords = [];
    tags.forEach((tag) => {
      let tagList = tagToLogicTable[nowLang][tag];
      tagList.forEach((t) => {
        keywords.push(t);
      })
    });
    return keywords;
  }

  /**
   * @desc . ()true
   * @param {array} ruleArr 
   * @param {string} code 
   * @return {boolean, string} isInclude, errorPattern
   */
  static isInRule(ruleArr, code) {
    if (ruleArr.length != 0) {
      let includeKeyFlag = true;

      let errorNum = 0;
      let errorPattern = '';
      for(let i=0; i<ruleArr.length; i++) {
        if(code.indexOf(ruleArr[i])<0) { // error, codepattern
          errorNum = i+1;
          errorPattern = ruleArr[i];
          includeKeyFlag = false;
          break;
        }
      }
      // ruleArr.forEach((key) => { 
        
      // });

      let obj = {
        'isInclude': includeKeyFlag,
        'errorNum': errorNum,
        'errorPattern': errorPattern,
      }
      return obj;

      // if(includeKeyFlag) {
        
      //   return obj;
      // } else {
      //   obj = {
      //     'isInclude': '',
      //     'errorPattern': errorPattern
      //   }
      //   return obj;
      // }
    }
  }

}